import randomSleep from '../utilities/_randomSleep';
import initialData from './data';
const KEY = 'DB';
export const loadContents = async (folder) => {
    await randomSleep();
    const jsonString = localStorage.getItem(KEY);
    let data;
    if (jsonString) {
        data = JSON.parse(jsonString);
    }
    else {
        data = initialData;
    }
    let fetchedItems = [];
    if (!folder) {
        fetchedItems = data.filter(item => item.folderId < 0);
    }
    else {
        const targetFolder = data.find(item => item.name === folder);
        if (targetFolder) {
            fetchedItems = data.filter(item => item.folderId > -1 && item.folderId === targetFolder.id);
        }
    }
    const mappedItems = fetchedItems.map(item => {
        if (!item.isFolder) {
            /* File */
            return {
                id: item.id,
                name: item.name,
                isFolder: item.isFolder,
                folderId: item.folderId,
                type: item.type,
                createdAt: new Date(item.createdAt),
                createdBy: item.createdBy,
                updatedAt: new Date(item.updatedAt),
                updatedBy: item.updatedBy,
            };
        }
        /* Node */
        return {
            id: item.id,
            name: item.name,
            isFolder: item.isFolder,
            folderId: item.folderId,
            createdAt: new Date(item.createdAt),
            createdBy: item.createdBy,
            updatedAt: new Date(item.updatedAt),
            updatedBy: item.updatedBy,
        };
    });
    // Data from localStorage is empty - Initialize data
    if (jsonString === null) {
        localStorage.setItem(KEY, JSON.stringify(initialData));
    }
    return mappedItems;
};
